.Header {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding: 5px 50px;
  background-image: linear-gradient(to right, rgba(52, 37, 106, 1), rgba(107, 67, 156, 1));
}

.HeaderInner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}

.HeaderLogo {
  margin-right: 100px;
  color: #ffffff;
  font-weight: 700;
  font-size: 30px;
  text-decoration: none;
}
